import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { AuthGuard } from './user/auth.guard';
import { SelectivePreloadStrategy } from './SelectivePreloadStrategy';
import { LoginComponent } from './user/login/login.component';
import { RegisterComponent } from './user/register/register.component';
import { StoryListComponent } from './story/story-list/story-list.component';
import { AddStoryComponent } from './story/add-story/add-story.component';
import { MyStoryListComponent } from './story/my-story-list/my-story-list.component';
import { StoryDetailComponent } from './story/story-detail/story-detail.component';
import { StoryResolver } from './story/StoryResolver';
import { FavoriteStoryListComponent } from './story/favorite-story-list/favorite-story-list.component';


const appRoutes: Routes = [
  { path: 'login', component: LoginComponent },
  { path: 'register', component: RegisterComponent },
  { path: 'story/list', component: StoryListComponent },
  { path: 'story/myList', component: MyStoryListComponent, canActivate: [AuthGuard] },
  { path: 'story/favoriteList', component: FavoriteStoryListComponent, canActivate: [AuthGuard] },
  { path: 'story/add', component: AddStoryComponent, canActivate: [AuthGuard] },
  {
    path: 'story/:id', 
    component: StoryDetailComponent, 
    resolve: { story: StoryResolver }
  },
  { path: '', redirectTo: 'story/list', pathMatch: 'full' },
  { path: '**', component: PageNotFoundComponent },
];

@NgModule({
  declarations: [],
  imports: [
    CommonModule,
    RouterModule.forRoot(appRoutes, {
      preloadingStrategy: SelectivePreloadStrategy,
    }),
  ],
  exports: [RouterModule],
})
export class AppRoutingModule {}
