import { Chapter, ChapterJson } from './chapter.model';
import { NumberValueAccessor } from '@angular/forms';

interface StoryJson {
    id: number,
    name: string,
    favorites: number,
    author: string,
    dateAdded: Date,
    chapters: ChapterJson[],
}

export class Story {
    private _id;

    constructor(
        private _name: string,
        private _favorites = 1,
        private _author = '',
        private _dateAdded = new Date(),
        private _chapters = new Array<Chapter>(),
    ) {}

    static fromJSON(json: StoryJson): Story {
        const rec = new Story(
            json.name, 
            json.favorites,
            json.author,
            new Date(json.dateAdded),
            json.chapters.map(Chapter.fromJSON), 
        );
        rec._id = json.id;
        return rec;
    }

    toJSON(): StoryJson {
        return <StoryJson> {
            id: this.id,
            name: this.name,
            chapters: this.chapters.map(ing => ing.toJSON()),
            favorites: this.favorites,
            dateAdded: this.dateAdded,
            author: this.author
        };
    }

    get name(): string {
        return this._name;
    }

    get chapters() : Chapter[] {
        return this._chapters;
    }

    get dateAdded() {
        return this._dateAdded;
    }

    get favorites() {
        return this._favorites;
    }

    get author() {
        return this._author;
    }

    get id() {
        return this._id;
    }

    addChapters(chapterlist: Chapter[]) {
        chapterlist.forEach(chapter => {
            this._chapters.push(chapter);
        });
    }

    addChapter(name: string, text: string, author?: string, number?: number) {
        const currentDate = new Date();
        this._chapters.push(new Chapter(name, number, currentDate, text, author));
    }
}