import { Pipe, PipeTransform } from '@angular/core';
import { Story } from './story.model';

@Pipe({
  name: 'storyFilter'
})
export class StoryFilterPipe implements PipeTransform {
  transform(stories: Story[], name: string): Story[] {
    if (!name || name.length === 0) {
      return stories;
    }
    return stories.filter(rec =>
      rec.name.toLowerCase().startsWith(name.toLowerCase())
    );
  }
}