import { StoryDataService } from './../story-data.service';
import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import {
  FormGroup,
  FormControl,
  Validators,
  FormBuilder,
  FormArray,
  ReactiveFormsModule
} from '@angular/forms';
import { debounceTime, distinctUntilChanged, catchError } from 'rxjs/operators';
import { EMPTY } from 'rxjs';
import { Story } from '../story.model';
import { Chapter } from '../chapter.model';

@Component({
  selector: 'app-add-story',
  templateUrl: './add-story.component.html',
  styleUrls: ['./add-story.component.css'],
})
export class AddStoryComponent implements OnInit {
  public story: FormGroup;
  public errorMessage: string = '';
  public confirmationMessage: string = '';

  constructor(
    private fb: FormBuilder,
    private _storyDataService: StoryDataService
  ) {}

  ngOnInit() {
    this.story = this.fb.group({
      storyName: ['', [Validators.required, Validators.minLength(2)]],
      chapterName: [''],
      inhoud: ['']
    });
  }

  onSubmit() {
    this._storyDataService
      .addNewStory(new Story(this.story.value.storyName, 1, "", new Date(), new Array(new Chapter(this.story.value.chapterName, 1, new Date(), this.story.value.inhoud, ""))))
      .pipe(
        catchError((err) => {
          this.errorMessage = err;
          return EMPTY;
        })
      )
      .subscribe((rec: Story) => {
        this.confirmationMessage = `a story for ${rec.name} was successfully added`;
      });

    this.story = this.fb.group({
      storyName: ['', [Validators.required, Validators.minLength(2)]],
      chapterName: [''],
      inhoud: ['']
    });
  }

  getErrorMessage(errors: any): string {
    if (!errors) {
      return null;
    }
    if (errors.required) {
      return 'is required';
    } else if (errors.minlength) {
      return `needs at least ${errors.minlength.requiredLength} characters (got ${errors.minlength.actualLength})`;
    } else if (errors.amountNoName) {
      return `if amount is set you must set a name`;
    }
  }
}
