import { Injectable, OnInit } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { map, catchError, tap, shareReplay, switchMap } from 'rxjs/operators';
import { Story } from './story.model';
import { Observable, throwError, BehaviorSubject, of } from 'rxjs';
import { environment } from 'src/environments/environment';
import { Chapter } from './chapter.model';

@Injectable({
  providedIn: 'root',
})
export class StoryDataService {
  private _stories$ = new BehaviorSubject<Story[]>([]);
  private _stories: Story[];
  private _chapters: Chapter[];
  allChapters$: any;

  constructor(private http: HttpClient) {
    this.stories$
      .pipe(
        catchError((err) => {
          // temporary fix, while we use the behaviorsubject as a cache stream
          this._stories$.error(err);
          return throwError(err);
        })
      )
      .subscribe((stories: Story[]) => {
        this._stories = stories;
        this._stories$.next(this._stories);
      });
  }

  get allStories$(): Observable<Story[]> {
    return this._stories$;
  }

  get myStories$(): Observable<Story[]> {
    return this.http.get(`${environment.apiUrl}/stories/MyStories`).pipe(
      tap(console.log),
      shareReplay(1),
      catchError(this.handleError),
      map((list: any[]): Story[] => list.map(Story.fromJSON))
    );
  }

  get favoriteStories$(): Observable<Story[]> {
    return this.http.get(`${environment.apiUrl}/author/favoriteStories`).pipe(
      tap(console.log),
      shareReplay(1),
      catchError(this.handleError),
      map((list: any[]): Story[] => list.map(Story.fromJSON))
    );
  }

  get stories$(): Observable<Story[]> {
    return this.http.get(`${environment.apiUrl}/stories/`).pipe(
      tap(console.log),
      shareReplay(1),
      catchError(this.handleError),
      map((list: any[]): Story[] => list.map(Story.fromJSON))
    );
  }

  getStory$(id: number): Observable<Story> {
    return this.http
      .get(`${environment.apiUrl}/stories/${id}`)
      .pipe(catchError(this.handleError), map(Story.fromJSON)); // returns just one recipe, as json
  }

  getChapters$(id: number): Observable<Chapter[]> {
    return this.http
      .get(`${environment.apiUrl}/stories/chapters/${id}`)
      .pipe(tap(console.log),
            shareReplay(1),
            catchError(this.handleError), 
            map((list: any[]): Chapter[] => list.map(Chapter.fromJSON)))
  }

  isFavorited$(id: number): Observable<boolean> {
    return this.http
      .get(`${environment.apiUrl}/author/isFavorited/${id}`)
      .pipe(tap(console.log),
            shareReplay(1),
            catchError(this.handleError))
  }

  favorite(story: Story) {
    return this.http
      .post(`${environment.apiUrl}/author/`, story.toJSON())
      .pipe(tap(console.log), catchError(this.handleError), map(Story.fromJSON));
  }

  addNewStory(story: Story) {
    return this.http
      .post(`${environment.apiUrl}/stories/`, story.toJSON())
      .pipe(catchError(this.handleError), map(Story.fromJSON))
      .pipe(
        // temporary fix, while we use the behaviorsubject as a cache stream
        catchError((err) => {
          this._stories$.error(err);
          return throwError(err);
        }),
        tap((rec: Story) => {
          this._stories = [...this._stories, rec];
          this._stories$.next(this._stories);
        })
      );
  }

  updateStory(story: Story) {
    return this.http
      .put(`${environment.apiUrl}/stories/${story.id}`, story.toJSON())
      .pipe(tap(console.log), catchError(this.handleError))
      .subscribe(() => {
        this._stories = this._stories.filter((rec) => rec.id != story.id);
        this._stories$.next(this._stories);
      });
  }

  deleteStory(story: Story) {
    return this.http
      .delete(`${environment.apiUrl}/stories/${story.id}`)
      .pipe(tap(console.log), catchError(this.handleError))
      .subscribe(() => {
        this._stories = this._stories.filter((rec) => rec.id != story.id);
        this._stories$.next(this._stories);
      });
  }

  handleError(err: any): Observable<never> {
    let errorMessage: string;
    if (err.error instanceof ErrorEvent) {
      errorMessage = `An error occurred: ${err.error.message}`;
    } else if (err instanceof HttpErrorResponse) {
      console.log(err);
      errorMessage = `'${err.status} ${err.statusText}' when accessing '${err.url}'`;
    } else {
      errorMessage = err;
    }
    return throwError(errorMessage);
  }
}
