import { Component, OnInit } from '@angular/core';
import { Story } from '../story.model';
import { StoryDataService } from '../story-data.service';
import { Subject, Observable, of, EMPTY, merge } from 'rxjs';
import {
  distinctUntilChanged,
  debounceTime,
  map,
  filter,
  catchError,
  scan,
} from 'rxjs/operators';
import { AuthenticationService } from 'src/app/user/authentication.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-my-story-list',
  templateUrl: './my-story-list.component.html',
  styleUrls: ['./my-story-list.component.css'],
})

export class MyStoryListComponent implements OnInit {
  public filterStoryName: string;
  public filterStory$ = new Subject<string>();
  private _fetchStories$: Observable<Story[]>;

  public errorMessage: string = '';

  constructor(private route: ActivatedRoute, private _storyDataService: StoryDataService) {
    this.filterStory$
      .pipe(
        distinctUntilChanged(),
        debounceTime(400),
        map((val) => val.toLowerCase())
      )
      .subscribe((val) => (this.filterStoryName = val));
  }

  ngOnInit(): void {
    this._fetchStories$ = this._storyDataService.myStories$.pipe(
      catchError((err) => {
        this.errorMessage = err;
        return EMPTY;
      })
    );
  }

  applyFilter(filter: string) {
    this.filterStoryName = filter;
  }

  get stories$(): Observable<Story[]> {
    return this._fetchStories$;
  }

  deleteStory(story: Story) {
    if (confirm("Are you sure you want to delete " + story.name)) {
      this._storyDataService.deleteStory(story);
      this._fetchStories$ = this._storyDataService.myStories$.pipe(
        catchError((err) => {
          this.errorMessage = err;
          return EMPTY;
        })
      );
    }
  }
}
