export interface ChapterJson {
    name: string;
    number: number;
    dateAdded: Date;
    text: string;
    author: string;
  }

export interface ChapterCreateJson {
  chapterName: string;
  text: string;
}


  export class Chapter {
    constructor(
      private _name: string,
      private _number = 1,
      private _dateAdded = new Date(),
      private _text: string,
      private _author: string
    ) {}
  
    static fromJSON(json: ChapterJson): Chapter {
      const ing = new Chapter(
        json.name, 
        json.number, 
        new Date(json.dateAdded), 
        json.text, 
        json.author);
      return ing;
    }

    static fromJSONCreate(json: ChapterCreateJson): Chapter {
      const ing = new Chapter(
        json.chapterName,
        1,
        new Date(),
        json.text,
        "");
      return ing;
    }
  
    toJSON(): ChapterJson {
      return { name: this.name, 
              number: this._number, 
              dateAdded: this._dateAdded, 
              text: this._text, author: 
              this._author };
    }
  
    get name() {
      return this._name;
    }
    get number() {
        return this._number;
    }

    get dateAdded() {
        return this._dateAdded
    } 
    
    get text() {
        return this._text;
    }

    get author() {
        return this._author;
    }
  }
  