import { Component, OnInit } from '@angular/core';
import { Story } from '../story.model';
import { StoryDataService } from '../story-data.service';
import { Subject, Observable, of, EMPTY, merge } from 'rxjs';
import {
  distinctUntilChanged,
  debounceTime,
  map,
  filter,
  catchError,
  scan,
} from 'rxjs/operators';
import { Router } from '@angular/router';

@Component({
  selector: 'app-story-list',
  templateUrl: './story-list.component.html',
  styleUrls: ['./story-list.component.css'],
})

export class StoryListComponent implements OnInit {
  public filterStoryName: string;
  public filterStory$ = new Subject<string>();
  private _fetchStories$: Observable<Story[]>;

  public errorMessage: string = '';

  constructor(private _storyDataService: StoryDataService, private _router: Router) {
    this.filterStory$
      .pipe(
        distinctUntilChanged(),
        debounceTime(400),
        map((val) => val.toLowerCase())
      )
      .subscribe((val) => (this.filterStoryName = val));
  }

  ngOnInit(): void {
    this._fetchStories$ = this._storyDataService.allStories$.pipe(
      catchError((err) => {
        this.errorMessage = err;
        return EMPTY;
      })
    );
  }

  applyFilter(filter: string) {
    this.filterStoryName = filter;
  }

  get stories$(): Observable<Story[]> {
    return this._fetchStories$;
  }
  
  showStory(story: Story) {
    this._router.navigateByUrl['/story/' + story.id];
  }
}
