import {
    Resolve,
    RouterStateSnapshot,
    ActivatedRouteSnapshot
  } from '@angular/router';
  import { Story } from './story.model';
  import { Injectable } from '@angular/core';
  import { StoryDataService } from './story-data.service';
  import { Observable } from 'rxjs';
  
  @Injectable({
    providedIn: 'root'
  })
  export class StoryResolver implements Resolve<Story> {
    constructor(private storyService: StoryDataService) {}

    resolve(
      route: ActivatedRouteSnapshot,
      state: RouterStateSnapshot
    ): Observable<Story> {
      return this.storyService.getStory$(route.params['id']);
    }
  }
  