import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { StoryDataService } from '../story-data.service';
import { Story } from '../story.model';
import { FormBuilder, FormGroup, Validators, FormArray } from '@angular/forms';
import { Chapter } from '../chapter.model';
import { catchError, debounceTime, distinctUntilChanged, delay } from 'rxjs/operators';
import { EMPTY, Observable } from 'rxjs';


@Component({
  selector: 'app-story-detail',
  templateUrl: './story-detail.component.html',
  styleUrls: ['./story-detail.component.css']
})
export class StoryDetailComponent implements OnInit {
  public story: Story;
  public chapterfg: FormGroup;
  public errorMessage: string = '';
  public confirmationMessage: string = '';
  private _fetchChapters$: Observable<Chapter[]>;
  private favorited$: Observable<boolean>;
  private _isLoading: boolean;

  constructor(
    private route: ActivatedRoute,
    private fb: FormBuilder,
    private _storyDataService: StoryDataService
  ) {
    this._isLoading = true;
  }

  ngOnInit() {
    this.route.data.subscribe(item => (this.story = item['story']));

    this._fetchChapters$ = this._storyDataService.getChapters$(this.story.id).pipe(
      catchError((err) => {
        this.errorMessage = err;
        return EMPTY;
      })
    );

    this.favorited$ = this._storyDataService.isFavorited$(this.story.id).pipe(
      catchError((err) => {
        this.errorMessage = err;
        return EMPTY;
      })
    );

    this.chapterfg = this.fb.group({
      chaptersforms: this.fb.array([ this.createChapters() ])
    });

    this.chaptersforms.valueChanges
      .pipe(debounceTime(400), distinctUntilChanged())
      .subscribe(ingList => {
        const lastElement = ingList[ingList.length - 1];
        if ( lastElement.chapterName && lastElement.text.length > 2 ) { 
          this.chaptersforms.push(this.createChapters()); 
         }else if (ingList.length >= 2) {
          const secondToLast = ingList[ingList.length - 2];
          if (
            !lastElement.chapterName &&
            !lastElement.text &&
            (!secondToLast.chapterName || secondToLast.text.length < 2)
          ) {
            this.chaptersforms.removeAt(this.chaptersforms.length - 1);
          }
        }
      });
  }

  createChapters(): FormGroup {
    return this.fb.group({
      chapterName: ['', [Validators.minLength(3)]],
      text: ['']
    });
  }

  get chaptersforms(): FormArray {
    return <FormArray>this.chapterfg.get('chaptersforms');
  }

  get allChapters$(): Observable<Chapter[]> {
    return this._fetchChapters$;
  }

  onSubmit() {
    let chapters = this.chapterfg.value.chaptersforms.map(Chapter.fromJSONCreate);
    chapters = chapters.filter((ing) => ing.name != "");
    this.story.addChapters(chapters);

    this._storyDataService
      .updateStory(this.story)

    this.chapterfg = this.fb.group({
      chaptersforms: this.fb.array([ this.createChapters() ])
    });
  }

  getErrorMessage(errors: any): string {
    if (!errors) {
      return null;
    }
    if (errors.required) {
      return 'is required';
    } else if (errors.minlength) {
      return `needs at least ${errors.minlength.requiredLength} characters (got ${errors.minlength.actualLength})`;
    } else if (errors.amountNoName) {
      return `if amount is set you must set a name`;
    }
  }

  ngAfterViewInit() { 
    this._isLoading = false; 
  }

  get isLoading() {
    return this._isLoading;
  }

  get isFavorited$() {
    return this.favorited$;
  }

  async favoriteStory() {
    this._storyDataService.favorite(this.story).pipe(
      catchError((err) => {
        this.errorMessage = err;
        return EMPTY;
      }) 
    ).subscribe(
      story => this.favorited$ = this._storyDataService.isFavorited$(this.story.id).pipe(
      catchError((err) => {
        this.errorMessage = err;
        return EMPTY;
      })
    ));
  }
}
